
'use client';

import { useState, useEffect } from 'react';
import { db } from '@/lib/firebase';
import { collection, onSnapshot, query, type Query, type DocumentData, type CollectionReference } from 'firebase/firestore';

export function useFirestoreQuery<T>(q: Query | CollectionReference) {
  const [data, setData] = useState<T[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!q) return;

    setIsLoading(true);

    const unsubscribe = onSnapshot(
      q,
      (querySnapshot) => {
        const results: T[] = querySnapshot.docs.map((doc) => ({
          ...doc.data(),
          id: doc.id,
        } as T));
        setData(results);
        setIsLoading(false);
      },
      (err) => {
        console.error("Firestore query error:", err);
        setError(err.message);
        setIsLoading(false);
      }
    );

    return () => unsubscribe();
  }, [JSON.stringify(q)]); // Re-run effect if query changes

  return { data, isLoading, error };
}
